.diva-wrapper {
  min-height: 100%;
  width: 100%;
  flex-grow: 1;
}
.diva-wrapper.diva-wrapper-flexbox {
  display: flex;
  flex-direction: column;
}
.diva-wrapper > *:not(.diva-outer) {
  flex-shrink: 0;
}
.diva-title {
  text-align: center;
  font-weight: bold;
  font-size: 1.3em;
  max-width: 50em;
  margin: 0 auto;
}
.diva-button,
.diva-input {
  box-sizing: border-box;
  outline: none;
  border-radius: 0;
  font: inherit;
  vertical-align: middle;
}
.diva-button::-moz-focus-inner,
.diva-input::-moz-focus-inner {
  padding: 0;
}
.diva-button {
  display: inline-block;
  cursor: pointer;
  height: 34px;
  width: 34px;
  line-height: 34px;
  -webkit-appearance: none;
  padding: 0;
  background-color: #f1f1f1;
  background-repeat: no-repeat;
  background-position: center;
  border: 1px solid #aaaaaa;
}
.diva-button:hover,
.diva-button:focus {
  background-color: #fdfdfd;
}
.diva-button.diva-button-text {
  width: auto;
  padding: 0 0.5em;
}
.diva-input {
  height: 34px;
  border: 1px solid #aaaaaa;
  padding: 0 0.5em;
}
.diva-input:focus {
  border-color: #919191;
}
.diva-label {
  margin: 0 1em;
  line-height: 34px;
  vertical-align: top;
  white-space: nowrap;
}
.diva-tools-left,
.diva-tools-right {
  margin-bottom: 1em;
}
.diva-tools-left {
  float: left;
  user-select: none;
}
.diva-zoom-out-button,
.diva-grid-out-button {
  background-image: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABYAAAAWCAYAAADEtGw7AAAAIklEQVR42mNgGAWjYBQQB3x9ff9TgkcNJmzwKBgFowAVAACElnmBtJelAAAAAABJRU5ErkJggg==");
}
.diva-zoom-in-button,
.diva-grid-in-button {
  background-image: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABYAAAAWCAYAAADEtGw7AAAAKElEQVR42mNgGAWkAF9f3//IeNTgkWQwugGk4lGDqRf2o+l41OCRAgDiStSheMjqggAAAABJRU5ErkJggg==");
  margin-left: -1px;
}
.diva-tools-right {
  float: right;
}
.diva-tools-right .diva-button {
  margin-left: -1px;
  user-select: none;
}
.diva-page-nav {
  white-space: nowrap;
  margin-right: 1em;
}
.diva-goto-form {
  display: inline-block;
  position: relative;
}
.diva-goto-page-input {
  width: 5em;
}
.diva-input-suggestions {
  display: none;
  box-sizing: border-box;
  position: absolute;
  width: 100%;
  background-color: white;
  border: solid 1px #aaa;
  border-top: none;
  z-index: 1;
}
.diva-input-suggestion {
  box-sizing: border-box;
  width: 100%;
  margin-top: 3px;
  padding: 2px;
  padding-left: 5px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  cursor: pointer;
}
.diva-input-suggestion.active,
.diva-input-suggestion:hover {
  background-color: #dfefff;
}
.diva-fullscreen-icon {
  background-image: url("data:image/png;base64,");
}
.diva-toggle-nonpaged-icon {
  background-image: url("data:image/png;base64,");
}
.diva-toggle-nonpaged-icon-active {
  background-image: url("data:image/png;base64,");
}
.diva-view-menu {
  display: inline-block;
  position: relative;
}
.diva-view-options {
  position: absolute;
  top: 34px;
  z-index: 1;
  display: none;
  box-shadow: 1px 1px 4px 0 rgba(0, 0, 0, 0.1);
}
.diva-view-options .diva-button {
  margin-top: -1px;
  display: block;
}
.diva-document-icon {
  background-image: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAwAAAAYCAYAAADOMhxqAAAABHNCSVQICAgIfAhkiAAAAAlwSFlzAAAN1wAADdcBQiibeAAAABl0RVh0U29mdHdhcmUAd3d3Lmlua3NjYXBlLm9yZ5vuPBoAAAAsSURBVDiNY/T19f3PQAJgIkUxAwMDA+P//yRZQIYNNPfDqIZRDUNHw3DIcQDkTQvYVGzS1gAAAABJRU5ErkJggg==");
}
.diva-grid-icon {
  background-image: url("data:image/png;base64,");
}
.diva-book-icon {
  background-image: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABYAAAAWCAYAAADEtGw7AAAAGXRFWHRTb2Z0d2FyZQBBZG9iZSBJbWFnZVJlYWR5ccllPAAAADtJREFUeNpi/P//PwMtABMDjQDNDGbx8/MjKiw2bdrECKKJVT/0gmLU4FGDRw0eNXhADWYcrZpgACDAAPfJDBBti7cPAAAAAElFTkSuQmCC");
}
.diva-link-icon {
  background-image: url("data:image/png;base64,");
}
.diva-zoom-label,
.diva-grid-label,
.diva-page-label {
  display: inline-block;
}
.diva-fullscreen-tools {
  position: fixed;
  top: 15px;
  right: 30px;
  z-index: 101;
  padding: 15px;
  border: 1px solid #dddddd;
  background: #ffffff;
  box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.5);
  /* prevent variable number of digits in labels from resizing .diva-fullscreen-tools */
}
.diva-fullscreen-tools .diva-tools-left,
.diva-fullscreen-tools .diva-tools-right {
  margin-bottom: 0;
}
.diva-fullscreen-tools .diva-tools-right {
  margin-left: 2em;
}
.diva-fullscreen-tools .diva-label,
.diva-fullscreen-tools .diva-page-label {
  margin: 0;
}
.diva-fullscreen-tools .diva-zoom-label,
.diva-fullscreen-tools .diva-grid-label,
.diva-fullscreen-tools .diva-page-label {
  width: 8em;
  display: block;
}
.diva-fullscreen-tools .diva-page-nav {
  margin-right: 0;
}
.diva-outer {
  display: flex;
  clear: both;
  background: #f1f1f1;
  border: 1px solid #aaaaaa;
  position: relative;
  overflow: hidden;
  min-height: 100px;
  min-width: 200px;
  height: 700px;
  flex-grow: 1;
}
.diva-viewer-canvas {
  position: absolute;
  top: 0;
  left: 0;
}
.diva-viewport {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: auto;
}
.diva-inner {
  cursor: url("data:application/octet-stream;base64,"), pointer;
  position: relative;
  overflow: hidden;
  margin: 0 auto;
}
.diva-inner.diva-grabbing {
  cursor: url("data:application/octet-stream;base64,"), move !important;
}
.diva-page {
  box-shadow: 7px 7px 6px -6px rgba(0, 0, 0, 0.3);
  position: absolute;
  background-color: #fdfdfd;
}
.diva-page-tools-wrapper {
  position: absolute;
  z-index: 3;
}
.diva-page-tools {
  position: relative;
  top: -25px;
  height: 25px;
}
.diva-page-tools div {
  display: inline-block;
  height: 25px;
  width: 25px;
  cursor: pointer;
}
.diva-fullscreen {
  width: 100% !important;
  max-width: 100% !important;
  height: 100% !important;
  max-height: 100% !important;
  z-index: 100;
  position: fixed !important;
  top: 0;
  left: 0;
  margin: 0;
  border: 0;
}
.diva-popup {
  background: #ffffff;
  border: 1px solid #aaaaaa;
  padding: .6em;
  z-index: 101;
  position: absolute;
  box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.5);
}
.diva-link-popup {
  font-size: 10pt;
}
.diva-link-popup input {
  width: 18em;
}
.diva-link-popup input:focus {
  outline: none;
}
.diva-link-popup.in-fullscreen {
  top: 150px;
  right: 30px;
}
.diva-hide-scrollbar {
  overflow: hidden !important;
}
.diva-slider {
  position: relative;
  top: 0.6em;
  float: left;
}
@media (max-width: 480px) {
  .diva-slider {
    width: 100px;
  }
}
.diva-throbber {
  display: none;
  position: absolute;
  width: 50px;
  height: 50px;
  background: url("data:image/gif;base64,") no-repeat center;
  background-color: #ffffff;
  border: 1px solid #dddddd;
  box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.5);
  top: 50%;
  left: 50%;
  margin-left: -25px;
  margin-top: -25px;
}
.diva-error {
  position: absolute;
  width: 400px;
  height: 200px;
  background-color: #fff;
  border: 1px solid #ddd;
  box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.5);
  top: 50%;
  left: 50%;
  margin-left: -220px;
  margin-top: -120px;
  z-index: 120;
  padding: 0 1em;
}
.diva-error-close {
  width: 18px;
  height: 18px;
  border: none;
  background: #ffffff url("data:image/png;base64,");
  position: absolute;
  top: 1em;
  right: 1em;
}
.diva-error-close:hover {
  background-image: url("data:image/png;base64,");
  cursor: hand;
}
.diva-modal {
  position: absolute;
  width: 32em;
  background-color: #fff;
  border: 1px solid #ddd;
  box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.5);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 120;
  padding: 2em 2.2em;
}
/* Begin canvas plugin */
.diva-canvas-icon {
  background: url("data:image/png;base64,") no-repeat;
}
.diva-canvas-icon:hover {
  background: url("data:image/png;base64,") no-repeat;
}
.diva-canvas-icon.new {
  background: url("data:image/png;base64,") no-repeat;
}
.diva-canvas-icon.new:hover {
  background: url("data:image/png;base64,") no-repeat;
}
#diva-canvas-backdrop {
  z-index: 105;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: none;
  background: rgba(50, 50, 50, 0.9);
}
#diva-canvas-tools {
  color: #333333;
  position: fixed;
  top: 10px;
  left: 10px;
  width: 230px;
  z-index: 108;
  background: #ffffff;
  padding-bottom: 10px;
  box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.5);
}
#diva-canvas-tools .action-buttons {
  clear: both;
  margin-bottom: 5px;
  text-align: right;
}
#diva-canvas-tools .action-buttons a {
  color: #333333;
  text-decoration: none;
  background: #f1f1f1;
  padding: 5px;
  border: 1px solid #aaaaaa;
}
#diva-canvas-tools .action-buttons a:last-child {
  border-left: 0;
}
#diva-canvas-tools .action-buttons a:hover {
  background: #fdfdfd;
}
#diva-canvas-toolbar {
  height: 16px;
  padding: 10px;
  padding-bottom: 0;
}
#diva-canvas-toolbar div {
  height: 18px;
  width: 18px;
  background-repeat: no-repeat;
  cursor: pointer;
  z-index: 109;
  display: inline-block;
  margin-right: 5px;
}
#diva-canvas-toolbar span {
  vertical-align: top;
  float: right;
}
#diva-canvas-toolwindow {
  position: relative;
  margin: 10px;
}
#diva-canvas-close {
  background-image: url("data:image/png;base64,");
}
#diva-canvas-close:hover {
  background-image: url("data:image/png;base64,");
}
#diva-canvas-minimise {
  background-image: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABIAAAASCAYAAABWzo5XAAAAGXRFWHRTb2Z0d2FyZQBBZG9iZSBJbWFnZVJlYWR5ccllPAAAAFlJREFUeNpi/P//PwM1ABMDlcCoQUPRIBZfX9+jjIyM2kA2I9RgRjTMgEYjY5j6ayxAQ6yo4CAt6nkNiG8AsQYQ/4fif0hsWP5Bpv9jUXudcTSvDUGDAAIMAJ9XGBGo0H6VAAAAAElFTkSuQmCC");
}
#diva-canvas-minimise:hover {
  background-image: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABIAAAASCAYAAABWzo5XAAAAGXRFWHRTb2Z0d2FyZQBBZG9iZSBJbWFnZVJlYWR5ccllPAAAAE9JREFUeNpi/P//PwM1ABMDlcCoQUPRIEYgPgrE2lA2E5RGxgxoNDKGqb8GIv4PKq+xAPENINaAuuwflEbGDGg0Moapv844mteGoEEAAQYAhHkXuTBtU6sAAAAASUVORK5CYII=");
}
#diva-canvas-buttons div {
  cursor: pointer;
  background-repeat: no-repeat;
  width: 20px;
  height: 20px;
  padding: 5px;
  display: inline-block;
  background-position: center center;
}
#diva-canvas-buttons div.clicked {
  background-color: #dddddd;
}
#diva-canvas-buttons .contrast {
  background-image: url("data:image/png;base64,");
}
#diva-canvas-buttons .contrast:hover,
#diva-canvas-buttons .contrast.clicked {
  background-image: url("data:image/png;base64,");
}
#diva-canvas-buttons .brightness {
  background-image: url("data:image/png;base64,");
}
#diva-canvas-buttons .brightness:hover,
#diva-canvas-buttons .brightness.clicked {
  background-image: url("data:image/png;base64,");
}
#diva-canvas-buttons .rotation {
  background-image: url("data:image/png;base64,");
}
#diva-canvas-buttons .rotation:hover,
#diva-canvas-buttons .rotation.clicked {
  background-image: url("data:image/png;base64,");
}
#diva-canvas-buttons .zoom {
  background-image: url("data:image/png;base64,");
}
#diva-canvas-buttons .zoom:hover,
#diva-canvas-buttons .zoom.clicked {
  background-image: url("data:image/png;base64,");
}
#diva-canvas-buttons .red {
  background-image: url("data:image/png;base64,");
}
#diva-canvas-buttons .red:hover,
#diva-canvas-buttons .red.clicked {
  background-image: url("data:image/png;base64,");
}
#diva-canvas-buttons .green {
  background-image: url("data:image/png;base64,");
}
#diva-canvas-buttons .green:hover,
#diva-canvas-buttons .green.clicked {
  background-image: url("data:image/png;base64,");
}
#diva-canvas-buttons .blue {
  background-image: url("data:image/png;base64,");
}
#diva-canvas-buttons .blue:hover,
#diva-canvas-buttons .blue.clicked {
  background-image: url("data:image/png;base64,");
}
#diva-canvas-pane {
  background: #dddddd;
  padding-bottom: 10px;
}
#diva-canvas-pane p {
  margin-left: 10px;
  padding-top: 10px;
}
#diva-canvas-pane p .link {
  cursor: pointer;
}
#diva-canvas-pane p .link:hover {
  text-decoration: underline;
}
#diva-canvas-mode {
  text-transform: capitalize;
}
#diva-canvas-slider {
  width: 188px;
  margin: 0 auto;
  display: block;
}
#diva-canvas-slider .handle {
  margin-left: -6px;
}
#diva-canvas-minimap {
  height: 210px;
  width: 210px;
  cursor: crosshair;
  margin-bottom: 10px;
  background: #000000;
}
#diva-canvas {
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  z-index: 107;
  cursor: url("data:application/octet-stream;base64,"), pointer;
}
#diva-map-viewbox {
  border: 2px solid #aaaaaa;
  position: absolute;
  top: 10px;
  left: 10px;
  cursor: crosshair;
  user-select: none;
  display: none;
}
.overflow-hidden {
  overflow: hidden !important;
}
#diva-canvas-wrapper {
  z-index: 106;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  overflow: scroll;
}
.canvas-throbber {
  z-index: 110;
  position: fixed;
}
/* End canvas plugin */
/* Begin download plugin */
.diva-download-icon {
  background: url("data:image/png;base64,") no-repeat;
}
.diva-download-icon:hover {
  background: url("data:image/png;base64,") no-repeat;
}
/* End download plugin */
/* Begin autoscroll plugin */
.diva-autoscroll-icon {
  background-image: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABAAAAASCAYAAABSO15qAAAAGXRFWHRTb2Z0d2FyZQBBZG9iZSBJbWFnZVJlYWR5ccllPAAAAGhJREFUeNpiYGBgKABigf///zOQg0EARH4A4gZyDIIZ8B/JoAJKDIDhB0CcQIkBRBtEyABkgxwoMQCGD6AbRKoBGAYxQgXIBRuZGKgAKPIC3QLxArnRSHZCIjspk52ZKMrOFBUoAAEGAKnq593MQAZtAAAAAElFTkSuQmCC");
}
.diva-autoscroll-prefs {
  display: none;
  margin-right: .6em;
}
.diva-autoscroll-prefs-text {
  float: left;
}
.diva-autoscroll-prefs-input {
  float: right;
  margin-left: .6em;
}
.diva-autoscroll-pps {
  width: 9em;
}
/* Begin iiif-highlight plugin */
.annotation-overlay {
  padding: 1px;
}
.diva-annotations-icon {
  float: right;
  border-right: none;
  background-image: url("data:image/png;base64,");
}
.annotations-icon-active {
  background-image: url("data:image/png;base64,");
}
.diva-inner.annotations-hidden .diva-highlight {
  display: none;
}
/* End iiif-highlight plugin */
/* Begin iiif-metadata plugin */
.metadata-label {
  font-weight: bold;
}
.diva-metadata-link {
  font-size: 1em;
  line-height: 1.8em;
}
/* End iiif-metadata plugin */
